.text-font__small {
  font-size: 14px;
  line-height: 16px;
  font-weight: @deafult-weight__regular;
}

.text-forn__button {
  font-size: 14px;
  line-height: 24px;
  font-weight: @deafult-weight__medium;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
